</template>

<script>
  import Header from '@/components/Header.vue'
  import NavBread from '@/components/NavBread.vue'
  import Footer from '@/components/Footer.vue'
    export default {
      name: "Address",
      components: {
        Header,
        NavBread,
        Footer
      },
      data(){
        return {
          addressList : [],
          checkedAddressId : '',
          selectAddressId : ''
        }
      },
      created(){
        this.getAddress()
      },
      methods:{
        //获取收货地址
        getAddress(){
          this.$http.post('/users/addressList').then((res)=>{
            this.addressList = res.data.result
            //获取收获默认地址id
            this.addressList.forEach((item)=>{
              if(item.isDefault == true){
                this.checkedAddressId = item.addressId

              }
            })
          })
        },
        //设置默认地址
        setDefault(addressId){
          this.$http.post('/users/setDefault',{
            addressId : addressId
          }).then((res)=>{

            // this.checkedAddress =res
            this.getAddress()

          })
        }

      }
    }
</script>

